.my-slide {
  display: flex;
  height: 100%;
  color: white;
  justify-content: center;
  align-items: center;
  background-size: cover;
  position: relative;
  background-position: center;
}
.slides {
  height: 100%;
  max-height: 100%;
  transition: all 0.5s ease;
}

.my-slide::before {
  display: block;
  content: "";
  /* background-color: rgba(0, 0, 0, 0.5); */
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  position: absolute;
  z-index: 0;
}

.my-slide h2 {
  position: relative;
  opacity: 0;
  transition: all 600ms ease;
  transition-delay: 300ms;
}

.my-slide.active h2 {
  opacity: 1;
}

.buttons {
  display: flex;
  width: 100%;
  justify-content: center;
  position: absolute;
  bottom: 10px;
}

.my-prev-button,
.my-next-button {
  background: transparent;
  outline: none !important;
  border: none;
  margin: 0 5px;
  cursor: pointer;
  color: white;
  transition: all 0.5s ease;
}

.dots {
  margin: 0;
  padding: 0;
  position: absolute;
  top: 0;
  right: 30px;
  height: 100%;
  list-style: none;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.dots li button {
  font-size: 0;
  background: transparent;
  border: 0;
  outline: none;
  cursor: pointer;
  transition: transform 0.3s ease;
}

.dots li button::before {
  font-size: 26px;
  content: "•";
  color: #888;
}

.dots li.active button::before {
  color: transparent;
  border: 2px solid white;
  border-radius: 50%;
  content: "";
  font-size: 0;
  padding: 4px;
}

.my-prev-button:disabled,
.my-next-button:disabled {
  opacity: 0.5;
  color: white !important;
}

.my-prev-button:hover,
.my-next-button:hover {
  color: orange;
}

